

html,body
{
margin: 1px;
background-image: repeating-linear-gradient(90deg, hsla(57,0%,42%,0.09) 0px, hsla(57,0%,42%,0.09) 1px,transparent 1px, transparent 60px),repeating-linear-gradient(0deg, hsla(57,0%,42%,0.09) 0px, hsla(57,0%,42%,0.09) 1px,transparent 1px, transparent 60px),repeating-linear-gradient(0deg, hsla(57,0%,42%,0.09) 0px, hsla(57,0%,42%,0.09) 1px,transparent 1px, transparent 10px),repeating-linear-gradient(90deg, hsla(57,0%,42%,0.09) 0px, hsla(57,0%,42%,0.09) 1px,transparent 1px, transparent 10px),linear-gradient(90deg, rgb(20,20,20),rgb(20,20,20));
@media screen and (max-width: 415px) {
    
    overflow-x: hidden;
    overflow-y: scroll;
    width: 100%;
    height: 100%;
    body {
        position: relative;
        -webkit-overflow-scrolling:touch;
      }
}
@media only screen and (device-width: 768px) {
    overflow-x: hidden;
    overflow-y: scroll;
    width: 100%;
    height: 100%;
    body {
        position: relative;
        -webkit-overflow-scrolling:touch;
      }
}

}



.Nav-Header
{
 display: flex;
 flex-direction: row;
}
.btn-header
{
    padding: 20px;
    margin: 1vh;
    border: none;
    background-color:transparent ;
    color: white;
    font-size: 20px;
    transition: 0.25s color ease-in;
    cursor: pointer;
    @media screen and (max-width: 415px) {
        font-size: 15px;
        padding: 2px;
    }
}

.btn-header:hover{
    color:gold;
    border:none;
    transition-delay: 10ms;
}
.btn-header:active{
    outline: none;
    border:none;
}
.btn-header:focus{
    outline:none;
}

.img-responsive{
    border-radius: 50%;
    border: solid goldenrod;
   margin-right: 200px; 
   @media screen and (max-width: 415px) {
   width: auto;
   height: 200px;
   position:absolute;
   margin-right: 0 auto;
   vertical-align: middle;
   left: 7em;
   right: 7em;
   top:10em;
  
    @media screen and (max-width: 375px) {
       // left: 5em;
    }
    @media screen and (max-width: 320px) {
        left: 5em;
        top:5em;
    }
   
    }
    @media only screen and (device-width: 768px) {
        width: auto;
        height: 25em;
        position:absolute;
        margin-right: 0;
        vertical-align: middle;
        left:14em;
        top:10em;
    }
    

    
}
@media screen and (min-width: 800px) {
.ProfilePic{
    display: flex;
    flex-direction: row-reverse;
    margin:1em;

}
}

.ProfileContent{
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    text-align: left;
    align-items: stretch;
    align-content: space-between;
    padding: 1em;
    @media screen and (max-width: 415px) {
        vertical-align: middle;
        position: relative;
        text-align: center;
        top:20em;
        left:1em;
        @media screen and (max-width: 320px) {
            top:16em;
        }
     }

     @media only screen and (device-width: 768px) {
        vertical-align: middle;
        position: relative;
        text-align: center;
        top:30em;
        left:13em;
     }
}
.name-banner{
    color: white;
    font-size: 45px;
    font-weight: bold;
    font-family: 'Times New Roman', Times, serif;
    @media screen and (max-width: 415px) {
       font-size: 3em;
     }
     @media screen and (max-width: 375px) {
        font-size: 2em;
     }
     @media screen and (max-width: 320px) {
        font-size: 2em;
    }
}
.sub-banner{
    color: white;
    font-size: 20px;
    font-weight: normal;
    padding-top: 1em;
    font-family: 'Times New Roman', Times, serif;
}
.quote-banner{
    color: white;
    font-size: 15px;
    white-space: nowrap;
    font-weight: normal;
    background-color: rgba(255 , 255, 255, 0.075);
    border-radius: 4px;
   
    @media screen and (max-width: 360px) {
        font-size: 13px;
     }
     @media screen and (max-width: 375px) {
        font-size: 14px;
     }
     @media screen and (max-width: 320px) {
        font-size: 11px;
    }
    
}

.Header-Intro{
    min-height: 100vh;
    z-index: 1;
}

.ParallaxSection{
    background-image: url('../Resource/code.png');
    position: relative;
    border-radius: 4px;
    min-height: 500px;
    width: 100%;
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: center;
    border-bottom: solid 7px rgba(179, 138, 3, 0.413);
    z-index: 1;
    @media screen and (max-width: 415px) {
        min-height: 200px;
        
      }
}


#experienceSection{
    display: flex;
    flex-flow: row;
    min-height: 150vh;
    height: auto;
    width: 100%;
    @media screen and (max-width: 415px) {
        flex-flow: column;
        min-height: 500px;
       
      }

      @media only screen and (device-width: 768px) {
        flex-flow: column;
        min-height: 500px;
     }
     @media only screen and (min-device-width: 481px) and (max-device-width: 1024px) and (orientation:landscape) {
        flex-flow: column;
        min-height: 500px;
      }
}




header{
    max-height: 100px;
   
    height: 20vh;
    width: 100%;
}

header > h2{
    color: white;
    padding: 0.5em;
    text-align: center;
    font-size: 30px;
}

@media screen and (min-width: 800px) {
.PanelCards{
   text-align: left;
   word-spacing: 1px;
   padding: 1em;
   width: 30%;
   height: 133vh; 
   border-radius: 1em;
   margin-left: 1em;
   box-shadow: 1px 2px 2px 0 #aaa;
   background-image: repeating-linear-gradient(0deg, rgba(0,0,0, 0.06) 0px, rgba(0,0,0, 0.06) 1px,transparent 1px, transparent 16px),repeating-linear-gradient(90deg, rgba(0,0,0, 0.06) 0px, rgba(0,0,0, 0.06) 1px,transparent 1px, transparent 16px),linear-gradient(90deg, rgb(247,255,204),rgb(247,255,204));
   transition: all 1s cubic-bezier(.63,.15,.03,1.12);
   position: absolute; left: 35%;
   &.card-1{ 
    z-index: 10; transform: rotateZ(-2deg);
  }
   &.card-2{ 
    z-index: 9; transform: rotateZ(-7deg);
    transition-delay: 0.05s;
  }
    &.card-3{ 
    z-index: 8; transform: rotateZ(5deg);
    transition-delay: 0.1s;
  }

  
 
}
ol>li:not(:first-child) , ul>li{
    padding-top: 7px;
}

 
.PanelCardsNotFlip{
   text-align: left;
   word-spacing: 1px;
   padding: 1em;
