   transition: all 1s cubic-bezier(0.86, 0, 0.07, 1);
}

}

@media screen and (max-width: 415px) {
    .PanelCards,.PanelCardsNotFlip{
        text-align: left;
        word-spacing: 1px;
        margin-bottom: 1em;
        padding: 10px;
        height: auto; 
        border-radius: 1em;
        margin-left: 1em;
        margin-right: 1em;
